/*==========以下部分是Validform必须的===========*/

.Validform_checktip {
margin-left: 8px;
line-height: 20px;
height: 20px;
overflow: hidden;
color: #999;
font-size: 12px;
position: absolute;
top: -35px;
left: -63px;
z-index: 2;
height: 30px;
background-color: #fff;
}

.valid-arrow-bottom {
display: none;
position: absolute;
z-index: 2;
width: 0;
height: 0;
top: -6px;
left: 6px;
border: 6px solid red;
border-right-color: transparent;
border-left-color: transparent;
border-bottom-color: transparent;
}


.valid-arrow-bottom:after {
content: '\00a0';
position: absolute;
width: 0;
height: 0;
border: 8px solid #fff;
right: -8px;
top: -9px;
border-right-color: transparent;
border-left-color: transparent;
border-bottom-color: transparent;
}

.Validform_wrong {
color: red;
padding: 0 5px 0 5px;
white-space: nowrap;
border: solid 1px red;
height: 30px;
line-height: 30px;
min-width: 80px;
text-align: center;
}


.Validform_right{
	color:#71b83d;
	padding-left:20px;
}

.Validform_loading{
	padding-left:20px;	
}
.Validform_error{
	
}
#Validform_msg{color:#7d8289; font: 12px; width:280px; -webkit-box-shadow:2px 2px 3px #aaa; -moz-box-shadow:2px 2px 3px #aaa; background:#fff; position:absolute; top:0px; right:50px; z-index:99999; display:none;filter: progid:DXImageTransform.Microsoft.Shadow(Strength=3, Direction=135, Color='#999999');}
#Validform_msg .iframe{position:absolute; left:0px; top:-1px; z-index:-1;}
#Validform_msg .Validform_title{line-height:25px; height:25px; text-align:left; font-weight:bold; padding:0 8px; color:#fff; position:relative; background-color:#000;}
#Validform_msg a.Validform_close:link,#Validform_msg a.Validform_close:visited{line-height:22px; position:absolute; right:8px; top:0px; color:#fff; text-decoration:none;}
#Validform_msg a.Validform_close:hover{color:#cc0;}
#Validform_msg .Validform_info{padding:8px;border:1px solid #000; border-top:none; text-align:left;}

