.tabcontent(){
    overflow: auto;
    position: relative;
    z-index: 10;
    background-color:  @color-white;
    overflow: hidden;
    margin-bottom: 18px;
    &.transparent {
        background-color: transparent;
    }
    
}

.tabs-below .tab-content {
    .tabcontent()
}

.tabs-left .tab-content {
    .tabcontent()
}

.tabs-right .tab-content {
    .tabcontent()
}


