.accordion-group {
    border: 0px;
}
.accordion-heading {
    background-color: @color-primary-grey;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    min-height: 35px;
    a {
        color: #505458;
        &.collapsed {
            color: #8E9AA2;
        }
        &:hover {
            &.collapsed {
                color: #505458;
            }
        }
    }
    .collapsed {
        background-color: #d9e0e4;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        i {
            font-family: 'FontAwesome';
            float: right;
            position: relative;
            top: 4px;
            &:before {
                content: "\f055";
            }
        }
    }
    i {
        font-family: 'FontAwesome';
        float: right;
        position: relative;
        top: 4px;
        &:before {
            content: "\f056";
        }
    }
    .accordion-toggle {
        padding: 12px 18px;
    }
}
